}

.ivu-btn-ghost.ivu-btn-success:hover {
    color: $success-color !important;
    background: rgba(254, 245, 243, .5) !important;
}

.ivu-btn-ghost.ivu-btn-warning {
    color: $warn-color;
}

.ivu-btn-ghost.ivu-btn-warning:hover {
    color: $warn-color !important;
    background: rgba(254, 245, 243, .5) !important;
