//选项卡
.ivu-tabs {
    height: 100%;
}

.ivu-tabs-card {
    //border: 1px solid $cancel-color;
}

.ivu-tabs-bar {
    margin: 0;
}

.ivu-tabs.ivu-tabs-card > .ivu-tabs-bar .ivu-tabs-tab {
    margin-right: 0 !important;
    border-color: $cancel-color;
    border-radius: 4px 4px 0 0;
}

.ivu-tabs-tab + .ivu-tabs-tab {
    border-left: none !important;
}

.ivu-tabs-content {
    height: calc(100% - 36px);
    padding: 8px 16px 16px;
}

.ivu-tabs.ivu-tabs-card > .ivu-tabs-content {
    border: 1px solid $cancel-color;
    border-top: none;
}

.ivu-tabs-nav .ivu-tabs-tab-active {
    color: $base-color !important;
}

//弹出层
.ivu-modal-header {
    height: 42px;
    padding: 11px 20px;
}

.ivu-modal-close {
    height: 32px;
    width: 32px;
    right: 20px;
}

.ivu-icon-ios-close {
    font-size: 16px;
}

.ivu-modal-body {
    padding: 16px 20px 20px;
}

.ivu-modal-footer {
    //border-top: none;
}

.cw-modal-title {
    font-size: 20px;
    font-weight: bold;
}

.cw-modal-content {
    margin-top: 15px;
}

.ivu-modal {
    top: 60px;
}

.cw-message-modal, .cw-message-modal1, .cw-message-modal2 {

    .ivu-modal {
        top: 60px;
    }

    .ivu-modal-close {
        right: 8px;
    }

    .ivu-modal-footer {
        display: none;
    }
}

.cw-message-modal1, .cw-message-modal2 {
    .ivu-icon {
        vertical-align: sub;
    }

    .ivu-modal-header-inner {
        font-weight: normal;
    }
}

.cw-message-modal2 {
    .ivu-modal-body {
        display: none;
    }

    .ivu-modal-close {
        right: 20px;
        top: 11px;
    }

    .ivu-modal-header {
        border: none;
    }

    .ivu-icon-ios-close {
        font-size: 20px;
    }
}

.cw-message-modal1 .ivu-modal-header-inner {
