// 基础颜色统一配置
$head-theme: 'light'; //选择 light 或 blue
$headerHeight: 60px;

$base-color: #399CFF; //导航主⾊ 导航⽂字 重要按钮 链接⽂本
$base-hover: #61B0FF; //主色hover
$bask-dark: #0D90FC;
$chart-first: #88c4ff; //图表颜色
$chart-second: #b0d7ff; //图表颜色
$bask-tips-border: #d7ebff; //提示框边框
$base-card: #e9f5ff; //卡片，信息提示窗背景
$base-color-blue: #399CFF; //蓝色主题的导航主⾊ 导航⽂字 重要按钮 链接⽂本

$primary-color: #3fc6ef; //次要按钮
$primary-hover: #63D6FA; //hover

$info-color: #40C6EF; //次要按钮
$info-hover: #66D1F2; //hover
$info-border: #d8f4ff; //边框颜色
$info-back: #ebf9ff; //背景颜色


$success-color: #3de586; //操作成功 谨慎使⽤ 成功信息
$success-hover: #59D58F; //hover
$success-border: #DEF6E8; //边框颜色
$success-back: #EEFAF3; //背景颜色

$warn-color: #FEA30D; //警告，谨慎使用
$warn-hover: #F8CC64; //hover
$warn-border: #FFEDCF; //边框颜色
$warn-back: #FEF5E6; //背景颜色

$error-color: #ED3F3F; //操作警告 信息提醒 失败信息
$error-hover: #F39082; //hover
$error-border: #FBD9D9; //边框颜色
$error-back: #FDEBEB; //背景颜色

$cancel-color: #C9C9C9; //取消选择 边框颜⾊
$cancel-hover: #A9A9A9; //hover

$nav-color: #90BFE1; //导航hover

$sup-color: #F4F9FA; //辅色

$font-color: #282A2B; //主要⽂字 标题

$font-second: #5F6568; //常规文字 正文

$font-third: #888E94; //次要文字

$border-color: #DADEE2; //边框

$Dividing-line-color: #E7EBEE; //分割线

$background-color: #EFF2F5; //背景

$white-color: #fff; //白色

$auxiliary-color: #F7F8F9; //辅助背景

//顶部导航栏样式
$head-menu-color: if($head-theme=='light', #fff, $base-color);
$background-color: if($head-theme=='light', $base-color, $base-color-blue);
$color: if($head-theme=='light', #fff, #fff);
$focus-color: if($head-theme=='light', #909399, #fff);
$background-color-hover: if($head-theme=='light', #fff, $base-color-blue);
$color-hover: if($head-theme=='light', $base-color, #fff);
$font-display: if($head-theme=='light', $font-second, #fff);
$menu-hover: if($head-theme=='light', $base-color, #fff);
$header-font: if($head-theme=='light', $font-second, #D7EBFF);

.el-menu {
    background-color: $head-menu-color;
}

.el-menu-item {
    font-size: 14px;
    color: $font-display;
}

.el-menu-demo li.el-submenu div.el-submenu__title {
    font-size: 14px !important;
}

.el-submenu__title i {
    color: inherit !important;
}

ul.el-menu--popup-bottom-start div li.el-menu-item {
    font-size: 14px;
}

.el-menu--popup .el-menu-item:hover {
    background-color: #ecf5ff !important;
    color: $base-color !important;
}

li.el-submenu div.el-submenu__title {
    font-size: 14px;
}

.el-menu--horizontal .el-menu .el-menu-item, .el-menu--horizontal .el-menu .el-submenu__title {
    color: $font-display;
    background-color: $head-menu-color;
}

.el-menu--horizontal > .el-menu-item, .el-submenu__title i, .el-menu--horizontal > .el-submenu .el-submenu__title {
    color: $header-font;
}

.el-menu--horizontal > .el-menu-item.is-active, .el-menu--horizontal > .el-submenu.is-active .el-submenu__title {
    border-bottom: 2px solid $menu-hover;
    color: $color-hover;
}

.el-menu--horizontal .el-menu .el-menu-item.is-active, .el-menu--horizontal .el-menu .el-submenu__title.is-active {
    background-color: #EFF2F5 !important;
    color: $base-color !important;
}

.el-menu--horizontal .el-menu-item:not(.is-disabled):focus, .el-menu--horizontal .el-menu-item:not(.is-disabled):hover {
    color: $font-display;
    background-color: #EFF2F5;
}

.el-dropdown-menu__item--divided:before, .el-menu, .el-menu--horizontal > .el-menu-item:not(.is-disabled):focus, .el-menu--horizontal > .el-menu-item:not(.is-disabled):hover, .el-menu--horizontal > .el-submenu .el-submenu__title:hover {
    color: $menu-hover;
    background-color: $head-menu-color;
}

.el-menu-vertical-demo {
    color: $font-second;
    background-color: #fff;

    .el-menu-item {
        color: $font-second;
    }

    .el-menu-item:hover {
        color: $base-color;
    }

    .el-menu--inline {
        background-color: #fff;
    }
}

.el-menu--vertical {
    .el-menu {
        background-color: #fff;
    }

    .el-menu-item {
        color: $font-second;
    }
}

.el-menu--horizontal .el-menu .el-submenu__title:hover {
    color: $color-hover;
}

.el-menu--horizontal .el-menu .el-menu-item.is-active, .el-menu--horizontal .el-menu .el-submenu.is-active > .el-submenu__title {
    color: $color-hover;
}

.el-menu--horizontal > .el-menu-item:not(.is-disabled):focus, .el-menu--horizontal > .el-menu-item:not(.is-disabled):hover, .el-menu--horizontal > .el-submenu .el-submenu__title:hover {
    border-bottom: 2px solid $menu-hover !important;
}

.el-menu.el-menu--horizontal {
    @media (max-width: 1920px) {
        height: 60px !important;
    }
    @media (min-width: 1920px) {
        height: 64px !important;
    }
}

.el-menu--horizontal > .el-submenu:focus .el-submenu__title {
    color: unset;
}

.el-menu--horizontal > .el-menu {
    background-color: #fff;
    color: $font-second;
}

.el-menu--horizontal > .el-menu--popup {
    .el-menu-item {
        background-color: #fff;
        color: $font-second;
    }
}
