// 基础颜色统一配置
$head-theme: 'light'; //选择 light 或 blue
$headerHeight: 60px;

$base-color: #399CFF; //导航主⾊ 导航⽂字 重要按钮 链接⽂本
$base-hover: #61B0FF; //主色hover
$bask-dark: #0D90FC;
$chart-first: #88c4ff; //图表颜色
$chart-second: #b0d7ff; //图表颜色
$bask-tips-border: #d7ebff; //提示框边框
$base-card: #e9f5ff; //卡片，信息提示窗背景
$base-color-blue: #399CFF; //蓝色主题的导航主⾊ 导航⽂字 重要按钮 链接⽂本

$primary-color: #3fc6ef; //次要按钮
$primary-hover: #63D6FA; //hover

$info-color: #40C6EF; //次要按钮
$info-hover: #66D1F2; //hover
$info-border: #d8f4ff; //边框颜色
$info-back: #ebf9ff; //背景颜色


$success-color: #3de586; //操作成功 谨慎使⽤ 成功信息
$success-hover: #59D58F; //hover
$success-border: #DEF6E8; //边框颜色
$success-back: #EEFAF3; //背景颜色

$warn-color: #FEA30D; //警告，谨慎使用
$warn-hover: #F8CC64; //hover
$warn-border: #FFEDCF; //边框颜色
$warn-back: #FEF5E6; //背景颜色

$error-color: #ED3F3F; //操作警告 信息提醒 失败信息
$error-hover: #F39082; //hover
$error-border: #FBD9D9; //边框颜色
$error-back: #FDEBEB; //背景颜色

$cancel-color: #C9C9C9; //取消选择 边框颜⾊
$cancel-hover: #A9A9A9; //hover

$nav-color: #90BFE1; //导航hover

$sup-color: #F4F9FA; //辅色

$font-color: #282A2B; //主要⽂字 标题

$font-second: #5F6568; //常规文字 正文

$font-third: #888E94; //次要文字

$border-color: #DADEE2; //边框

$Dividing-line-color: #E7EBEE; //分割线

$background-color: #EFF2F5; //背景

$white-color: #fff; //白色

$auxiliary-color: #F7F8F9; //辅助背景

// 左侧导航默认样式修改，慎改！！！
.homeSe {
    color: #399CFF !important;
    background-color: #ebf5ff !important;
    border-right: 2px solid #389bef;
}

.el-menu--collapse {
    width: 63px !important;
}

.ivu-menu-vertical .ivu-menu-item:hover, .ivu-menu-vertical .ivu-menu-submenu-title:hover {
    color: $base-color;
}

.ivu-menu li > ul a {
    font-size: 14px !important;
    padding-left: 65px !important;
}

.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
    background: #fff;
}

.ivu-menu-submenu {
    font-size: 12px !important;
}

.ivu-menu-item > i {
    padding-right: 5px !important;
    margin: 0 !important;
}

.ivu-menu-submenu-title span > i, .ivu-menu-submenu-title > i {
    margin: 0 !important;
    padding-right: 5px !important;
}

.second-menu {
    font-size: 12px !important;
    padding-left: 24px !important;
}

.second-selected > .ivu-menu-submenu-title {
    color: $base-color !important;
}

.second-selected > .el-submenu__title {
    color: $base-color !important;
    background-color: $white-color !important;

    .ivu-icon-ios-home-outline {
        color: $base-color !important;
    }

    .ios-construct-outline {
        color: $base-color !important;
    }

    .ios-git-merge {
        color: $base-color !important;
    }

    .ivu-icon-ios-browsers-outline {
        color: $base-color !important;
    }

    .el-icon-arrow-down {
        color: $base-color !important;
    }

    .ios-document-outline {
        color: $base-color !important;
    }

    .ivu-icon-ios-cog-outline {
        color: $base-color !important;
    }
}

.seOpen {
    color: #fff !important;
    background-color: #217FC3 !important;
}

.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):after {
    background: none !important;
}

.el-submenu__title:hover, .el-menu-item:hover {
    color: $base-color;

    i {
        color: $base-color;
    }
}

.el-submenu__title > span {
    font-size: 14px;
}

.el-menu {
    border-right: none;
}

#eleLeft {
    border-right: solid 1px #e6e6e6;
}

.el-menu-item.is-active {
    color: $white-color;

    i {
        color: #399CFF !important;
    }
}

.el-submenu__title.is-active, .el-menu-item.is-active {
    color: $white-color;

    i {
        color: $white-color;
    }
}

.el-menu-item, .el-submenu__title {
    //text-align: center;
    height: 48px;
    line-height: 48px;
}
.el-submenu .el-menu-item{
    height: 48px!important;
}
