// 基础颜色统一配置
$head-theme: 'light'; //选择 light 或 blue
$headerHeight: 60px;

$base-color: #399CFF; //导航主⾊ 导航⽂字 重要按钮 链接⽂本
$base-hover: #61B0FF; //主色hover
$bask-dark: #0D90FC;
$chart-first: #88c4ff; //图表颜色
$chart-second: #b0d7ff; //图表颜色
$bask-tips-border: #d7ebff; //提示框边框
$base-card: #e9f5ff; //卡片，信息提示窗背景
$base-color-blue: #399CFF; //蓝色主题的导航主⾊ 导航⽂字 重要按钮 链接⽂本

$primary-color: #3fc6ef; //次要按钮
$primary-hover: #63D6FA; //hover

$info-color: #40C6EF; //次要按钮
$info-hover: #66D1F2; //hover
$info-border: #d8f4ff; //边框颜色
$info-back: #ebf9ff; //背景颜色


$success-color: #3de586; //操作成功 谨慎使⽤ 成功信息
$success-hover: #59D58F; //hover
$success-border: #DEF6E8; //边框颜色
$success-back: #EEFAF3; //背景颜色

$warn-color: #FEA30D; //警告，谨慎使用
$warn-hover: #F8CC64; //hover
$warn-border: #FFEDCF; //边框颜色
$warn-back: #FEF5E6; //背景颜色

$error-color: #ED3F3F; //操作警告 信息提醒 失败信息
$error-hover: #F39082; //hover
$error-border: #FBD9D9; //边框颜色
$error-back: #FDEBEB; //背景颜色

$cancel-color: #C9C9C9; //取消选择 边框颜⾊
$cancel-hover: #A9A9A9; //hover

$nav-color: #90BFE1; //导航hover

$sup-color: #F4F9FA; //辅色

$font-color: #282A2B; //主要⽂字 标题

$font-second: #5F6568; //常规文字 正文

$font-third: #888E94; //次要文字

$border-color: #DADEE2; //边框

$Dividing-line-color: #E7EBEE; //分割线

$background-color: #EFF2F5; //背景

$white-color: #fff; //白色

$auxiliary-color: #F7F8F9; //辅助背景

// 组件默认样式的强制修改
.demo-spin-icon-load {
    animation: ani-demo-spin 1s linear infinite;
}

.v-modal {
    z-index: 999 !important;
}

.ivu-message {
    z-index: 9999999999999 !important;
}

.ivu-spin-fix {
    background-color: hsla(0, 0%, 100%, 0.4) !important;
}

.table-info-row {
    background-color: #2ecc71 !important;
    color: #fff;
}

.table-error-row {
    background-color: #ed4014 !important;
    color: #fff;
}

.table-run-row {
    padding: 0 !important;
}

.focusing {
    border: 0px !important;
}

.is-dark {
    color: #000 !important;
    background-color: #fff !important;
    border: 1px solid #f7f7f7 !important;
}

.el-tooltip__popper[x-placement^=right] .popper__arrow {
    border-right-color: #f7f7f7;
}

.el-tooltip__popper[x-placement^=right] .popper__arrow:after {
    border-right-color: #f7f7f7;
}

.el-tooltip {
    border: 0;
}

.el-tooltip__popper.is-dark {
    color: #000 !important;
    background-color: #f7f7f7 !important;
    border: 1px solid #f7f7f7 !important;
}

.ivu-tooltip-content {
    opacity: 0.93;
}

//th.ivu-table-column-center .ivu-checkbox-inner {
//    display: none;
//}

.el-form-item .el-form-item {
    margin-bottom: 10px !important;
}

.el-form-item {
    margin-bottom: 10px !important;
}

.CodeMirror {
    height: 360px !important;
}

.el-message {
    top: 8% !important;
    left: 50%;
}

.ivu-modal-wrap {
    z-index: 3200 !important;
}

.ivu-modal-mask {
    z-index: 3200 !important;
}

.ivu-input-search {
    background-color: #217FC3 !important;
}

.ivu-input-search:hover {
    background-color: #446DB3 !important;
}

.ivu-input:hover {
    //
}

.radius4 {
    border-radius: 4px;
}

.shadow1 {
    box-shadow: 1px 1px 1px #ccc;
}

.be-center {
    margin: 0 auto; /*水平居中*/
    position: relative;
    top: 50%; /*偏移*/
    transform: translateY(-50%);
}

.be-flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.be-dot {
    overflow: hidden;
    white-space: normal;
    text-overflow: ellipsis;
    text-align: center;
}

.edit-color {
    color: $warn-color;

    &:hover {
        color: $warn-hover !important;
        border: 1px solid $warn-hover !important;
    }
}

.detail-color {
    color: $base-color;
    padding: 0 16px 0 0 !important;

    &:hover {
        color: $base-hover !important;
        background-color: transparent !important;
    }
}

.delete-color {
    color: $error-color;

    &:hover {
        color: $error-hover !important;
        border: 1px solid $error-hover !important;
    }
}

.save-color {
    color: $success-color;

    &:hover {
        color: $success-hover !important;
        border: 1px solid $success-hover !important;
    }
}

.be-pointer {
    cursor: pointer;
}

.el-message {
    z-index: 4000 !important;
}

.el-tooltip__popper {
    z-index: 4000 !important;
}

.tipsStyleRed {
    margin-left: 5px;
    color: red;
}

//.ivu-select-dropdown {
//    left: 1318px !important;
//    top: 59px !important;
//}

.ivu-breadcrumb {
    color: $font-second;
}

.ivu-breadcrumb > span:last-child {
    color: $font-color;
}

.ivu-card:hover {
    box-shadow: 0 2px 6px rgba(0, 0, 0, .12);
}

.el-pagination.is-background .el-pager li:not(.disabled).active {
    background-color: #fff !important;
    color: $base-color !important;
    border: 1px solid $base-color !important;
}

.el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li {
    border-radius: 4px !important;
    border: 1px solid #e6e6e6 !important;
    background-color: #fff !important;
}

.infoStyle {
    background-color: #fff !important;
    border: 1px solid #3fc6ef !important;
    color: #3fc6ef !important;
}

.infoStyle:hover {
    background-color: #ebf5fe !important;
}

.infoStyle-border {
    border: 1px dashed #3fc6ef !important;
    color: #3fc6ef !important;
}

.infoStyle-text {
    background-color: transparent !important;
    border: none !important;
    color: #3fc6ef !important;
}

.infoStyle-text:hover {
    color: #63d6fa !important;
}

.infoStyle-border:hover {
    background-color: transparent !important;
    color: #3fc6ef !important;
}

.infoStyle-text:focus {
    border: none !important;
    box-shadow: none !important;
}

