//}

div::-webkit-scrollbar {
    width: 4px;
    color: #C5CACF;
    /**/
}

.ivu-modal-wrap {
    position: fixed !important;
    top: 0 !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    background-color: rgba(55, 55, 55, .6) !important;
    height: 100% !important;
}

.ivu-modal-no-mask {
    pointer-events: all !important;
}
