// 基础颜色统一配置
$head-theme: 'light'; //选择 light 或 blue
$headerHeight: 60px;

$base-color: #399CFF; //导航主⾊ 导航⽂字 重要按钮 链接⽂本
$base-hover: #61B0FF; //主色hover
$bask-dark: #0D90FC;
$chart-first: #88c4ff; //图表颜色
$chart-second: #b0d7ff; //图表颜色
$bask-tips-border: #d7ebff; //提示框边框
$base-card: #e9f5ff; //卡片，信息提示窗背景
$base-color-blue: #399CFF; //蓝色主题的导航主⾊ 导航⽂字 重要按钮 链接⽂本

$primary-color: #3fc6ef; //次要按钮
$primary-hover: #63D6FA; //hover

$info-color: #40C6EF; //次要按钮
$info-hover: #66D1F2; //hover
$info-border: #d8f4ff; //边框颜色
$info-back: #ebf9ff; //背景颜色


$success-color: #3de586; //操作成功 谨慎使⽤ 成功信息
$success-hover: #59D58F; //hover
$success-border: #DEF6E8; //边框颜色
$success-back: #EEFAF3; //背景颜色

$warn-color: #FEA30D; //警告，谨慎使用
$warn-hover: #F8CC64; //hover
$warn-border: #FFEDCF; //边框颜色
$warn-back: #FEF5E6; //背景颜色

$error-color: #ED3F3F; //操作警告 信息提醒 失败信息
$error-hover: #F39082; //hover
$error-border: #FBD9D9; //边框颜色
$error-back: #FDEBEB; //背景颜色

$cancel-color: #C9C9C9; //取消选择 边框颜⾊
$cancel-hover: #A9A9A9; //hover

$nav-color: #90BFE1; //导航hover

$sup-color: #F4F9FA; //辅色

$font-color: #282A2B; //主要⽂字 标题

$font-second: #5F6568; //常规文字 正文

$font-third: #888E94; //次要文字

$border-color: #DADEE2; //边框

$Dividing-line-color: #E7EBEE; //分割线

$background-color: #EFF2F5; //背景

$white-color: #fff; //白色

$auxiliary-color: #F7F8F9; //辅助背景

// 公共样式
// 外边距
.margin-0 {
    margin: 0 !important;
}

.margin-10 {
    margin: 10px;
}

.margin-20 {
    margin: 20px;
}

.margin-top5 {
    margin-top: 5px;
}

.margin-top10 {
    margin-top: 10px;
}

.margin-top15 {
    margin-top: 15px;
}

.margin-top20 {
    margin-top: 20px;
}

.margin-top25 {
    margin-top: 25px;
}

.margin-top30 {
    margin-top: 30px;
}

.margin-top40 {
    margin-top: 40px;
}

.margin-bottom10 {
    margin-bottom: 10px;
}

.margin-left10 {
    margin-left: 10px!important;
}

.margin-left20 {
    margin-left: 20px;
}

.margin-right10 {
    margin-right: 10px;
}

// 内边距
.padding-0 {
    padding: 0 !important;
}

.padding-5 {
    padding: 5px !important;
}

.padding-10 {
    padding: 10px !important;
}

.padding-16 {
    padding: 16px !important;
}

.padding-top10 {
    padding-top: 10px;
}

.padding-bottom10 {
    padding-bottom: 10px;
}

.padding-left10 {
    padding-left: 10px;
}

.padding-right10 {
    padding-right: 10px;
}

// 小手
.pointer {
    cursor: pointer;
}

// 浮动
.float-left {
    float: left;
}

.float-right {
    float: right;
}

// 文本
.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

//字体
.font-bold {
    font-weight: bold;
}

//特大标题 默认加粗
.font-32 {
    font-weight: bold !important;
    font-size: 32px !important;
}

//主标题 默认加粗
.font-20 {
    font-weight: bold !important;
    font-size: 20px !important;
}

//标题 默认加粗
.font-18 {
    font-size: 18px !important;
    font-weight: bold !important;
}

//小标题 默认加粗
.font-16 {
    font-size: 16px !important;
    font-weight: bold !important;
}

//正文
.font-14 {
    font-size: 14px !important;
}

//正文(小)
.font-13 {
    font-size: 13px !important;
}

//辅助文字(小)
.font-12 {
    font-size: 12px !important;
}


// ---------字体-----------
